:root {
  --color-white: #ffffff;
  --color-black: #000000;
  --dark-background: rgba(0, 0, 0, 0.60);
  --light-background: rgba(255, 255, 255, 0.24);
  --light-yellow: #FFC900;
  --linear-btn: linear-gradient(0deg, #266EE0 0.01%, #3A9EED 40.01%, #AEDDFF 100%);
